#section0{
  height: 100%;
  position: relative;
  touch-action: none;
  transform: translate3d(0px, 0px, 0px);
  transition: transform 700ms;
}

#section0 video.overlay-video  {
  position: absolute;
  right: 0;
  bottom: 0;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background-color: black;
  background-position: center center;
  background-size: cover;
  object-fit: cover;
  z-index: -100;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

#section0_mobile{
  display:none;
  height: 100%;
  position: relative;
  touch-action: none;
  transform: translate3d(0px, 0px, 0px);
  transition: transform 700ms;
}

#section0_mobile video.overlay-video-m {
  position: absolute;
  right: 0;
  bottom: 0;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background-color: black;
  background-position: center center;
  background-size: cover;
  object-fit: cover;
  z-index: -100;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

