/* 두 번째 탭 메뉴 스타일링 */

#tab-inner-slide {
  /* height: 400px; */
  padding: 20px 0;
  position: relative;
  /* border:10px solid red; */
}

.swiper-container_tabContent {
  width: 100%;
  height: 100%;
  overflow: hidden;
  /* border: 10px solid black; */
}

.swiper-container_tabContent .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  width: 30%;
}

.swiper-container_tabContent .swiper-slide figure{
  width:100%;
  /* border:10px solid purple; */
  position:relative;
}

.swiper-container_tabContent .swiper-slide figure img {
  width: 100%;
  height: 80%;
  object-fit: contain;
  filter: drop-shadow(0 10px 10px rgba(0, 0, 0, 0.3)); 
}

.swiper-container_tabContent .swiper-slide figure figcaption {
  text-align: center;
}

.swiper-container_tabContent .swiper-slide figure figcaption p.s-product-name{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-size: 28px;
  font-style: normal;
  color: #000;
  text-align: center;
}

.swiper-container_tabContent .swiper-slide figure figcaption p.s-product-price{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-size: 18px;
  font-style: normal;
  color: #666;
  text-align: center;
  padding:10px 0;
}

.swiper-container_tabContent .swiper-slide figure figcaption p.s-sale img{
  width:40px;
  height: 40px;
  position: absolute;
  top:10px;
  left:10px;
}


/* 탭 스타일링 */
.tab-container {
  width: 87%;
  margin: 0 auto;
  /* border: 10px solid blue; */
}

.tab-buttons {
  list-style: none;
  padding: 0;
  margin-bottom: 20px;
  /* border:10px solid red; */
  padding-top: 114px;
  text-align: center;
}

.tab-buttons li {
  cursor: pointer;
  margin-right: 5px;
  height: 50px;
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-size: 20px;
  font-style: normal;
  color: #333;
  padding: 0 50px;
  line-height: 50px;
  display:inline-block;
}

.tab-buttons li.active {
  font-weight: 500;
  color: #e64011;
}

.tab-container .s-anibox  h3{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-size: 40px;
  font-style: normal;
  color: #000;
  text-align: left;
  padding-top: 110px;
  text-align: center;
}

.tab-container .s-anibox  p{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-size: 16px;
  font-style: normal;
  color: #999;
  padding: 16px 0 0;
  text-align: center;
}

.tab-content .tab-pane {
  display: none;
}

.tab-content .tab-pane.active {
  display: block;
}


/* 제품 스타일링 */
.tab-container .tab-content .tab-pane {
  width:100%;
  /* border: 10px solid magenta; */
  overflow: hidden;
}

.tab-container .tab-content #tab1 div.s-product-title{
  width:100%;
  /* border:10px solid blue; */
  overflow: hidden;
}

.tab-container .tab-content #tab1 div.s-product-title h3{
  /* border:10px solid salmon; */
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-size: 35px;
  font-style: normal;
  color: #000;
  text-align: left;
  padding: 100px 0 0;
  float: left;
  /* border:10px solid teal; */
}

.tab-container .tab-content #tab1 div.s-product-title ul{
  text-align: right;
  float:right;
  display: flex;
  padding-top: 130px;
}

.tab-container .tab-content #tab1 div.s-product-title li{
  display: inline-block;
}

.tab-container .tab-content #tab1 div.s-product-title li a{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-size: 14px;
  font-style: normal;
  color: #666;
  text-align: center;
  /* border:10px solid firebrick; */
  padding: 0 6px;
}

.tab-container .tab-content .tab-pane .tab1-box{
  width:100%;
  height:100%;
  /* border:10px solid black; */
  overflow: hidden;
}

.tab-container .tab-content .tab-pane figure {
  height: 409px;
  border: 1px solid #ccc;
  border-radius: 5px;
  float: left;
  width: 24%;
  position:relative;
  margin-right: 1.3%;
  margin-top: 1.3%;
  overflow: hidden;
}

.tab-container .tab-content .tab-pane figure:nth-child(4), 
.tab-container .tab-content .tab-pane figure:nth-child(8), 
.tab-container .tab-content .tab-pane figure:nth-child(12), 
.tab-container .tab-content .tab-pane figure:nth-child(16) {
  margin-right:0;
}

.tab-container .tab-content .tab-pane figure img{
  /* border: 5px solid yellow; */
  width: 100%;
  height: 80%;
  object-fit: contain;
  filter: drop-shadow(0 10px 10px rgba(0, 0, 0, 0.3));
  transition: transform 0.4s cubic-bezier(0.25, 1, 0.5, 1), 
  filter 0.4s ease-in-out;
}

.tab-container .tab-content .tab-pane figure:hover img{
  /* border: 5px solid yellow; */
  width: 100%;
  height: 80%;
  object-fit: contain;
  filter: drop-shadow(0 10px 10px rgba(0, 0, 0, 0.3));
  transform: scale(1.08); /* 부드러운 확대 */
  filter: brightness(1.1); /* 약간 밝게 */
}

.tab-container .tab-content .tab-pane figure figcaption{
  text-align: center;
}

.tab-container .tab-content .tab-pane figure figcaption p.s-product-name{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-size: 28px;
  font-style: normal;
  color: #000;
  text-align: center;
}

.tab-container .tab-content .tab-pane figure figcaption p.s-product-price{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-size: 18px;
  font-style: normal;
  color: #666;
  text-align: center;
  padding:10px 0;
}

.tab-container .tab-content .tab-pane figure figcaption p.s-sale img{
  width:40px;
  height: 40px;
  position: absolute;
  top:10px;
  left:10px;
  transition: none !important; /* 애니메이션 제거 */
  transform: none !important; /* 크기 변화 제거 */
  filter: none !important; /* 밝기 효과 제거 */
  box-shadow: none !important; /* 그림자 제거 */
  pointer-events: none !important; /* 마우스 이벤트 제거 */
}

.tab-container .tab-content .tab-pane figure figcaption p.s-product-name2{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-size: 28px;
  font-style: normal;
  color: #000;
  text-align: center;
}

.tab-container .tab-content .tab-pane figure figcaption p.s-product-price2{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-size: 18px;
  font-style: normal;
  color: #666;
  text-align: center;
  padding:10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.tab-container .tab-content .tab-pane figure figcaption p.s-product-price2 span{
  font-weight: 400;
  font-size: 14px;
  font-style: normal;
  color: #666;
  display: inline-block;
  vertical-align: middle;
}

