header {
  /* border:10px solid blue; */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height:80px;
  z-index: 50;
}

.s-header{
  width:100%;
  /* border:10px solid red; */
}

.s-header .s-menu-bg{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 90px;
  background: rgba(0, 0, 0, 1);
  backdrop-filter: blur(10px);
  transition: all 0.5s ease-in-out;
  opacity: 1;
}

.s-header:hover .s-menu-bg {
  height: 350px;
  background: rgba(0, 0, 0, 0.8);
  opacity: 1;
}


.logo {
  /* border:1px solid red; */
  width:100px;
  display:block;
  position: absolute;
  top:34px;
  left:0;
}

.logo img {
  object-fit: cover;
}


.menu-bg {
  /* border:10px solid red; */
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background: rgba(0, 0, 0, 0);
  backdrop-filter: blur(10px);
  transition: all 0.5s ease-in-out;
  opacity: 0;
}

header:hover .menu-bg {
  height: 350px;
  background: rgba(0, 0, 0, 0.6);
  opacity: 1;
}

.inner{
  /* border:10px solid salmon; */
  width:87%;
  margin:0 auto;
  position: relative;
  max-width:2000px;
}

#nav {
  width: 100%;
  height:100%;
  /* border:5px solid pink; */
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul.menu {
  width: 100%;
  height:100%;
  /* border:5px solid yellow; */
  display: inline-block;
  list-style: none;
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-size: 20px;
  font-style: normal;
  color:#fff;
}

ul.menu li {
  /* border:5px solid green; */
  position: relative;
  margin: 0 30px;
  cursor: pointer;
  text-align: center;
}

.submenu {
  /* border:1px solid yellow; */
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 260px;
  padding: 10px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease-in-out;
}

ul.submenu li a.menu-title{
  /* border:1px solid red; */
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-size: 20px;
  font-style: normal;
  color:#fff;
}

ul.submenu li a.menu-subtitle{
  /* border:1px solid red; */
  /* background: yellowgreen; */
  cursor: pointer;
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-size: 18px;
  font-style: normal;
  color:#ccc;
  line-height: 40px;
  text-align: left;
}

.submenu li::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -5px;
  width: 0;
  height: 2px;
  background: #d64200;
  transition: width 0.4s ease-in-out, left 0.4s ease-in-out;
}

.submenu li:hover::after {
  width: 100%;
  left: 0;
}

header:hover .submenu {
  opacity: 1;
  visibility: visible;
}

.inner #nav #Infomenu{
  /* border:1px solid yellow; */
  position:absolute;
  top:32px;
  right:0;
}

.inner #nav #infomenu span{
  border:1px solid purple;
  margin-right: 20px;
  width:28px;
  height:28px;
  display:inline-block;
  }

.inner #nav #infomenu span a{
  display:block;
  width:100%;
  height:100%;
}

.inner #nav #infomenu span:nth-child(1){
  background:url(../assets/images/icon/icon1.svg) no-repeat 50% 50%;
  background-size: 90%; 
}

.inner #nav #infomenu span:nth-child(2){
  background:url(../assets/images/icon/shopping1.svg) no-repeat 50% 50%;
}

.inner #nav #infomenu span:nth-child(3){
  background:url(../assets/images/icon/user2.svg) no-repeat 50% 50%;
  margin-right: 0;
}

/* 스크롤 시 스타일 */
.scrolled .menu-bg {
  height: 80px;
  background: rgba(0, 0, 0, 0.4);
  opacity: 1;
}

/* 미디어 쿼리 */
@media all and (min-width:1280px) and (max-width:1600px){
  header {
    /* background:blue; */
  }

  .inner{
    /* border:10px solid salmon; */
    width: 97% !important;
    
  }
}

@media all and (min-width:320px) and (max-width:1279px){
  header{
    display:none;
  }
}
