body{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: 1em;
  background:black;
}

body.sub-bodybg{
  background-color: #fff;
}

body.sub-bodybg2{
  background-color: #1c1c1c;
}

a{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-size: 1em;
  font-style: normal;
  color:#fff;
}

header{
  width:100%; 
  height:80px;
  /* background-color: rgba(102,102,102,1); */
  position:fixed;
  z-index: 10;
  /* border:10px solid red; */
}

/* 서브 페이지 */
header.sub-h {
  background-color:#000;
  position:static;
}

header.sub-h div.inner nav ul li a{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  color:#fff;
  padding:0 20px;
  font-size:18px;
  /* background:firebrick; */
}

/* ======================== */

header div.inner{
  /* border:10px solid yellow; */
  width:87%;
  height:100%;
  margin:0 auto;
  position:relative;
  max-width:2000px;
}

/* 미디어 쿼리 */
/* @media all and (min-width:1280px) and (max-width:1440px){
  header div.inner{
    width: 90%;
  }
} */

header div.inner h1.pc-logo{
  /* border:1px solid red; */
  display:block;
  position:absolute;
  top:30px;
  left:0;
}

header div.inner h1.pc-logo img{
  width:100px;
  
}

header div.inner nav{
  /* border:5px solid rgb(235, 235, 246); */
  text-align: center;
}

header div.inner nav ul{
  /* border:3px solid pink; */
  padding-top:30px;
}

header div.inner nav ul li{
  /* border:1px solid green; */
  display:inline-block;
}

header div.inner nav ul li a{
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  color:#fff;
  padding:0 20px;
  font-size:1.1em;
  /* background:firebrick; */
}

header div.inner div#Infomenu{
  /* border:1px solid purple; */
  position:absolute;
  top:30px;
  right:0;
}

header div.inner div#Infomenu span{
  /* border:1px solid purple; */
  margin-right: 20px;
  width:25px;
  height:25px;
  display:inline-block;
  }

header div.inner div#Infomenu span a{
  display:block;
  width:100%;
  height:100%;
}

header div.inner div#Infomenu span:nth-child(1){
  background:url(../assets/images/icon/icon1.svg) no-repeat 50% 50%;
  background-size: 90%;
}

header div.inner div#Infomenu span:nth-child(2){
  background:url(../assets/images/icon/shopping1.svg) no-repeat 50% 50%;
}

header div.inner div#Infomenu span:nth-child(3){
  background:url(../assets/images/icon/user2.svg) no-repeat 50% 50%;
  margin-right: 0;
}

footer{
  width:100%;
  height:auto;
  /* border:10px solid tomato; */
}

footer.s-footer{
  background-color:#000;
}

footer div.inner{
  /* border:10px solid yellow; */
  width:87%;
  height:100%;
  margin:0 auto;
  overflow: hidden;
}

footer div.inner div.Left{
  /* border:10px solid rgb(222, 255, 222); */
  width:70%;
  height:100%;
  float:left;
}

footer div.inner div.Left div.footer-menu{
  /* border:10px solid pink; */
  overflow: hidden;
  padding-top:100px;
}

footer div.inner div.Left div.footer-menu ul{
  /* border:1px solid rgb(136, 250, 165); */
}

footer div.inner div.Left div.footer-menu ul li {
  display: inline-block;
  background:url(../assets/images/Line.gif) no-repeat 0 5px;
  height:18px;
  padding:0 20px;
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-style: normal;
  color:#fff;
  font-size:18px;
}

footer div.inner div.Left div.footer-menu ul li:first-child {
  background: none;
  padding-left:0;
}

footer div.inner div.Left div.footer-menu ul li.Legal a{
  font-weight: 500;
}

/* 태블릿, 모바일 버튼 */

footer div.inner div.Left div.tm-Select-Sony{
  display:none;
  /* border:10px solid turquoise; */
  width:100%;
  text-align: center;
}

footer div.inner div.Left div.tm-Select-Sony #Sonyfamily{
  /* border:5px solid tomato; */
  display:block;
  width:233px;
  height:54px;
  margin-bottom:10px;
  -o-appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
