}

footer div.inner div.Left div.legal-text{
  /* border:10px solid lime; */
  margin-top:10px;
}

footer div.inner div.Left div.legal-text ul{
  /* border:10px solid magenta; */
  text-align: left;
  display: inline-block;
  font-family: "Noto Sans KR", serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-style: normal;
  color:#fff;
  font-size:14px;
}

footer div.inner div.Left div.legal-text ul li{
  /* border:1px solid lightblue; */
  padding:2px 0;
}

footer div.inner div.Left div.legal-text ul li span{
  /* background:rgb(136, 250, 165); */
  color:#fff;
  padding-right:20px;
}

footer div.inner div.Left div.legal-text ul li span::after{
  background:url(../assets/images/arrow2.gif) no-repeat 0 0;
  content:"";
  display:inline-block;
  height:10px;
  width:10px;
  margin-left:10px;
}

footer div.inner div.Left address{
